<template>
    <div id="wrap">
        <AppHeader />
        <div id="myDiary">
            <SideBar />
            <div class="container">
                <div class="title">
                    <h1></h1>
                </div>
                <div class="slider-container">
                    <button class="slider-control left" @click="prevSlide"></button>
                    <div class="slider">
                        <div v-for="(entry, index) in diaryEntries" :key="index" class="myDiary02">
                            <div class="petProfile">
                                <div class="store">
                                    <p><strong> </strong></p>
                                    <p>{{ entry.title }}</p>
                                </div>
                                <div class="date">
                                    <p><strong> </strong></p>
                                    <p>{{ entry.bPhone }}</p>
                                </div>
                                <div class="date">
                                    <p><strong> </strong></p>
                                    <p>{{ getUsageDate(entry.rtDate) }} ({{ entry.rtTime }})</p>
                                </div>
                                <div class="petName">
                                    <p><strong> </strong></p>
                                    <p>{{ entry.dogName }}</p>
                                </div>
                                <div class="mypetImgSlider">
                                    <p><strong>    </strong></p>
                                    <img :src="entry.afterImg" alt="After Grooming Image">
                                </div>
                            </div>
                            <div class="context">
                                <div class="weight">
                                    <p> </p>
                                    <p>{{ entry.curruntWeight }}</p>
                                </div>
                                <div>
                                    <p> </p>
                                    <p>{{ entry.attitude }}</p>
                                </div>
                                <div>
                                    <p> </p>
                                    <p>{{ entry.rCondition }}</p>
                                </div>
                                <div>
                                    <p> ()</p>
                                    <p>{{ entry.tangle }}</p>
                                </div>
                                <div>
                                    <p>  </p>
                                    <p>{{ entry.disliked }}</p>
                                </div>
                                <div>
                                    <p> </p>
                                    <p>{{ entry.surcharge }}</p>
                                </div>
                                <div>
                                    <p> </p>
                                    <p>{{ entry.message }}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <button class="slider-control right" @click="nextSlide"></button>
                </div>
            </div>
        </div>
        <TopButton />
        <AppFooter />
    </div>
</template>

<script>
import AppHeader from "@/components/AppHeader.vue"
import AppFooter from "@/components/AppFooter.vue"
import SideBar from '@/components/SideBar.vue'
import TopButton from "@/components/TopButton.vue"
import '@/assets/css/manager/mydiary.css'
import axios from 'axios';

export default {
    name: "MyDiaryView",
    components: {
        AppHeader,
        AppFooter,
        SideBar,
        TopButton,
    },
    data() {
        return {
            diaryEntries: [], //    
            currentIndex: 0, //   
        };
    },
    created() {
        this.fetchDiaryEntries();
    },
    methods: {
        fetchDiaryEntries() {
            const uNo = this.$store.state.authUser.uNo; //   .
            axios({
                method: 'get',
                url: `${this.$store.state.apiBaseUrl}/api/jw/userDogList/${uNo}`,
                headers: { "Content-Type": "application/json; charset=utf-8" },
                responseType: 'json'
            }).then(response => {
                console.log("============================================");
                console.log('  :', response.data.apiData);
                this.diaryEntries = response.data.apiData; //   diaryEntries 
            }).catch(error => {
                console.error('  :', error);
            });
        },
        nextSlide() {
            if (this.currentIndex < this.diaryEntries.length - 1) {
                this.currentIndex++;
            }
        },
        prevSlide() {
            if (this.currentIndex > 0) {
                this.currentIndex--;
            }
        },
       //   
       getUsageDate(rtDate) {
            // : rtDate 'YYYY MM DD HH:mm:ss'  
            const parts = rtDate.split(' '); //   
            return parts[0]; // YYYY MM DD  
        },
    },
    watch: {
        currentIndex(newIndex) {
            const slider = this.$el.querySelector('.slider');
            slider.style.transform = `translateX(-${newIndex * 1259}px)`;
        }
    }
};
</script>
