<template>
    <div id="wrap">
        <div id="login-container">
            <div class="login-box">
                <router-link to="/">
                    <img class="con-logo" src="../../assets/images/logo.png" alt="Logo">
                </router-link>
                <div class="login-form">
                    <form @submit.prevent="login">
                        <div class="form-group-login">
                            <input type="text" id="id" name="id" placeholder="" v-model="userVo.uId">
                        </div>
                        <div class="form-group-login">
                            <input type="password" id="password" name="password" placeholder="" v-model="userVo.uPw">
                            <span class="toggle-password" @click="togglePasswordVisibility">
                                <i :class="showPassword ? 'fas fa-eye' : 'fas fa-eye-slash'"></i>
                            </span>
                        </div>
                        <button type="submit" class="loginBtn"></button>
                        <router-link to="/signup">
                            <button class="signinBtn"></button>
                        </router-link>

                        <p class="signup-link">   <a href="/mlogin" class="manager-signup"></a> !</p>
                        
                        <div class="sns01">
                            <a id="" @click="kakaoLogin()">
                                <img class="kakao" src="../../assets/images/kakao.png">
                            </a>
                            <div id="naver_id_login">
                                <a id="">
                                    <img class="naver" src="@/assets/images/naver.png">
                                </a>
                            </div>


                        </div>
                        <div class="sns02">
                            <a id="">
                                <img class="google" src="../../assets/images/google.png">
                            </a>
                            <a id="">
                                <img class="facebook" src="../../assets/images/facebook.png">
                            </a>
                        </div>
                    </form>
                </div>
            </div><!-- login-box -->
            <div class="login-image-container">
                <img src="../../assets/images/banner02.png" alt="Image">
            </div>
            <div class="login-footer">
                <ul class="login-footer-ul">
                    <li></li>
                    <li></li>
                    <li>  </li>
                    <li> </li>
                </ul>
                <p> 2024  . All Rights Reserved.</p>
            </div>
        </div><!-- login-container -->
    </div><!-- wrap -->
</template>

<script>
import "@/assets/css/potal/login.css"
import axios from "axios";

const clientId = "dc1j0BkcYhtjZfREkzlH";

// 2.     
const getKakaoToken = async (code) => {
    try {
        const data = {
            grant_type: "authorization_code",
            client_id: "0e2f4296c5ab39f843a685fe91dd9b8a", // REST API 
            redirect_uri: "http://localhost:8080/login",
            code: code,
        };

        const queryString = Object.keys(data)
            .map((k) => encodeURIComponent(k) + "=" + encodeURIComponent(data[k]))
            .join("&");

            console.log(queryString);

        const result = await axios.post(
            "https://kauth.kakao.com/oauth/token",
            queryString,
            {
                headers: {
                    "Content-type": "application/x-www-form-urlencoded;charset=utf-8",
                },
            }
        );
        console.log(result);
        return result;
    } catch (e) {
        console.log(e);
    return e;
  }
};

//3.   
const getKakaoUserInfo = async () => {
    let data = "";
    await window.Kakao.API.request({
        url: "/v2/user/me",
    })
    .then(function (response) {
        console.log(response);
        data = response;
    })
    .catch(function (error) {
        console.log(error);
    });
        console.log("  ", data);
    return data;
};


export default {
    name: "LoginView",
    components: {
    },
    data() {
        return {
            showPassword: false, //   ,
            userVo:{
                uId: "",
                uPw: "",
            }, 
            kakaoUser:{}
        };
    },
    methods: {

        // *****  / *****
        togglePasswordVisibility() {
            this.showPassword = !this.showPassword; //  / 
            const passwordInput = document.getElementById('password');
            if (this.showPassword) {
                passwordInput.type = 'text'; //  
            } else {
                passwordInput.type = 'password'; //  
            }
        },
        // *****  *****
        login(){
            // console.log(" ");
            console.log(this.userVo);

            axios({
                method: 'post', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/su/login`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                // params: {uId: this.userVo.uId}, //get   
                data: this.userVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //

                if(response.data.result == "success"){
                    //  
                    let authUser = response.data.apiData;

                    //token    
                    console.log(response.headers.authorization.split(" ")[1]);
                    //"Authorization Bearer dajofddjaoadfklh.adfafa"
                    const token = response.headers.authorization.split(" ")[1];

                    //vuex 
                    this.$store.commit("setAuthUser", authUser);
                    this.$store.commit("setToken", token);

                    this.$router.push("/");

                
                } else {
                    console.log(response.data.message);
                    alert("  .");
                }
                
            }).catch(error => {
                console.log(error);
            });


        },

        // *****   *****
        //    
        kakaoLogin() {
            console.log(" ");

            // 1.   
            window.Kakao.Auth.authorize({
                redirectUri : 'http://localhost:8080/login',
                prompt : 'login' //   
            })
        },

        // 2.  
        async setKakaoToken(code) {
            const { data } = await getKakaoToken(code);
            if (data.error) {
                console.log(data.error);
                return;
            }
            console.log(data);
            const access_token = data.access_token;
            window.Kakao.Auth.setAccessToken(access_token);
            await this.setUserInfo(access_token);
            this.$router.push({ path: "/login" });
        },

        // 3.   
        async setUserInfo(access_token) {
            const res = await getKakaoUserInfo();
            const userInfo = {
                uId: 'kakao '+ res.id,
                uName: res.kakao_account.profile.nickname,
                uProfile: res.kakao_account.profile.profile_image_url,
            };
            console.log(userInfo);
            this.kakaoUser = userInfo;

            this.$store.commit('setKakaoToken',access_token);

            this.kakaoCheck(this.kakaoUser);
        },  

        // 4.    
        kakaoCheck(kakaoUser){
            console.log(" ");
            console.log(kakaoUser);

            axios({
                method: 'post',
                url: `${this.$store.state.apiBaseUrl}/api/su/kakao`,
                headers: { "Content-Type": "application/json; charset=utf-8" },
                data: kakaoUser,
                responseType: 'json'
            }).then(response => {
                console.log(response.data.apiData);

                if(response.data.result == "success"){

                    //  
                    let authUser = response.data.apiData;

                    //token    
                    console.log(response.headers.authorization.split(" ")[1]);
                    const token = response.headers.authorization.split(" ")[1];

                    
                    //vuex 
                    this.$store.commit("setAuthUser", authUser);
                    this.$store.commit("setToken", token);

                    this.$router.push("/");

                
                } else {
                    console.log(response.data.message);
                    alert("  .");
                }
            }).catch(error => {
                console.log(error);
            });
        }

    },
    mounted() {
        this.naverLogin = new window.naver_id_login(
            clientId,
            "http://localhost:8080/login"  //   Callback URL
        );
        var state = this.naverLogin.getUniqState();
        this.naverLogin.setButton("green", 2, 40); // 
        this.naverLogin.setDomain("http://localhost:8080");
        this.naverLogin.setState(state);
        this.naverLogin.init_naver_id_login();
    },
    created(){

        //    
        if(this.$route.query.code != undefined){
            console.log("  ");
            console.log(this.$route.query.code);
            console.log(" ");
            this.setKakaoToken(this.$route.query.code);
        }
    }
};
</script>
