<template>
    <div id="wrap">
        <div id="signup-container">
            <div class="signup-box">
                <router-link to="/">
                    <img class="con-logo" src="../../assets/images/logo.png" alt="Logo">
                </router-link>
                <!--  -->
                <div class="signup-form">
                    <form @submit.prevent="signup" enctype="multipart/form-data">
                        <!--  -->
                        <div class="form-group preview-image" > 
                            <!--   -->
                            <img id="preview-pimage">
                            <!--    -->
                            <label for="input-file" class="pImgAddBtn"></label>
                            <!--  -->
                            <input type="file" id="input-file" class="upload-hidden" @change="handleFileUpload" >
                        </div>

                        <!--   -->
                        <div class="form-group">
                            <div class="input-container">
                                <input type="text" id="id" name="id" placeholder="" v-model="userVo.uId" >
                                <button type="button" @click="checkDuplicate" class="small-btn green-btn"></button>
                            </div>
                            <small v-if="isDuplicate === true" class="error-message"> .</small>
                            <small v-if="isDuplicate === false && userVo.uId.length > 0" class="success-message">  .</small>
                        </div>

                        <!--   -->
                        <div class="form-group">
                            <input type="password" id="password" name="password" placeholder="" v-model="userVo.uPw">
                        </div>
                        <div class="form-group">
                            <input type="password" id="confirmPassword" name="confirmPassword" placeholder=" "
                                v-model="confirmPassword" @input="checkPasswordMatch">
                            <br>
                            <small v-if="!isPasswordMatch" class="error-message">  .</small>
                            <small v-if="isPasswordValid && confirmPassword.length > 0"
                                :class="{ 'success-message': isPasswordValid }">
                                {{ isPasswordValid ? ' .' : '' }}
                            </small>
                        </div>

                        <!--  -->
                        <div class="form-group">
                            <input type="text" id="name" name="name" placeholder="" v-model="userVo.uName">
                        </div>

                        <!--   -->
                        <div class="form-group">
                            <div class="input-container">
                                <input type="tel" id="phoneNumber" name="phoneNumber" placeholder="" v-model="userVo.uPhone">
                                <button type="button" @click="sendVerificationCode" class="small-btn green-btn"></button>
                            </div>
                        </div>
                        <!--   -->
                        <div v-if="showVerificationInput" class="form-group">
                            <div class="input-container">
                                <input type="text" id="verificationCode" name="verificationCode" placeholder=" ">
                                <button type="button" @click="verifyCode" class="small-btn green-btn"></button>
                            </div>
                        </div>

                        <!--  -->
                        <div class="form-group" style="margin-bottom: 10px;">
                            <div class="input-container">
                                <input type="text" id="postalCode" placeholder="" v-model="userVo.uZipCode" readonly>
                                <button id="postcode" type="button" @click="openPostcode" class="small-btn green-btn"></button>
                            </div>
                        </div>
                        <!--  -->
                        <div class="form-group" style="margin-bottom: 10px;">
                            <input type="text" v-model="userVo.uAddress" placeholder="" readonly>
                        </div>
                        <div class="form-group" style="margin-bottom: 10px;">
                            <input type="text" v-model="userVo.uDetailAddress" placeholder=" ">
                        </div>

                        <!--        -->
                        <div id="google_recaptha">
                            <div class="g-recaptcha" data-sitekey="6LfZqNspAAAAAAze3c3G_KHi67Z2gKiF0WM58CG4"></div>
                        </div>
                        <!--        -->

                        <button type="submit" class="signupBtn"></button>
                    </form>
                </div>
            </div><!-- signup-box -->
            <div class="signup-footer">
                <ul class="signup-footer-ul">
                    <li></li>
                    <li></li>
                    <li>  </li>
                    <li> </li>
                </ul>
                <p> 2024  . All Rights Reserved.</p>
            </div>
        </div><!-- signup-container -->
    </div><!-- wrap -->
</template>

<script>
import "@/assets/css/potal/signup.css"
import axios from "axios";

export default {
    name: "SignView",
    components: {
    },
    data() {
        return {
            showVerificationInput: false, //     
            isDuplicate: null, //    -> ""     . 
            confirmPassword: "", //   
            isPasswordMatch: true, //   
            isPasswordValid: false, //    
            zonecode: "",
            roadAddress: "",
            detailAddress: "",
            userVo:{
                uId: "",
                uPw: "",
                uName:"",
                uPhone:"",
                uZipCode:"",
                uAddress:"",
                uDetailAddress:"",
                file: null
            },
        };
    },
    methods: {
        //   
        handleFileUpload(event) {
            const file = event.target.files[0];
            const reader = new FileReader();

            if (!file.type.match(/image\//)) {
                this.modalVisible = true;
                return;
            }

            reader.onload = () => {
                const imgSrc = reader.result;
                const imgElement = document.getElementById('preview-pimage'); //   <img>  
                imgElement.src = imgSrc; //     
            };

            reader.readAsDataURL(file);

            // FormData  
            this.userVo.file = file;
        },

        //    
        checkDuplicate() {
            // console.log("   .");

            axios({
                method: 'post', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/su/idcheck`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {uId: this.userVo.uId}, //get   
                // data: formData, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //

                if (response.data.apiData == 1) {
                    this.isDuplicate = true;
                } else {
                    this.isDuplicate = false;
                }
            }).catch(error => {
                console.log(error);
            });

        },

        //  
        signup() {
            console.log(this.userVo);

            if(this.userVo.uId == ""){
                window.alert(" ");
            }else if(this.isDuplicate== null){
                window.alert(" ");
            }else if(this.userVo.uPw == ""){
                window.alert(" ");
            }else if(this.confirmPassword == ""){
                window.alert(" ");
            }else if(this.userVo.uName == ""){
                window.alert(" ");
            }else if(this.userVo.uPhone == ""){
                window.alert(" ");
            }else if(this.userVo.uZipCode == ""){
                window.alert(" ");
            }else if(this.userVo.uDetailAddress == ""){
                window.alert(" ");
            }else{
                let formData = new FormData();
                
                for (let key in this.userVo) {
                    if(key == "file" &&  this.userVo["file"] == null) {
                        //  null null 
                    }else{
                        formData.append(key, this.userVo[key]);
                    }
                }
    
                axios({
                    method: 'post', // put, post, delete 
                    url: `${this.$store.state.apiBaseUrl}/api/su/signup`,
                    headers: { "Content-Type": "multipart/form-data" }, //
                    //params: usersVo, //get   
                    data: formData, //put, post, delete   JSON  
                    responseType: 'json' //
                }).then(response => {
                    console.log(response.data.apiData); //
    
                    if (response.data.apiData == 1) {
                        alert(".  .");
                        this.$router.push({ path: '/login', query: { name: this.userVo.uName } });
                    } else {
                        alert(" .");
                    }
                }).catch(error => {
                    console.log(error);
                });
            }

        },
        
        openPostcode() {
            new window.daum.Postcode({
                oncomplete: (data) => {
                    this.userVo.uZipCode = data.zonecode;
                    this.userVo.uAddress = data.roadAddress;
                },
            }).open();
        },
        
        //  
        sendVerificationCode() {
            // console.log("   .");
            if(this.userVo.uPhone ==""){
                window.alert(" .")
            }else{
                window.alert(" .")
                this.showVerificationInput = true;
            }
        },
        verifyCode() {
            console.log("   .");
        },
        checkPasswordMatch() {
            this.isPasswordMatch = this.userVo.uPw === this.confirmPassword;
            this.isPasswordValid = this.isPasswordMatch;
        },
        
    },
    mounted(){
        const script = document.createElement('script');
        script.src = 'https://www.google.com/recaptcha/api.js';
        script.async = true;
        document.getElementById('google_recaptha').appendChild(script);
    }
};
</script>