<template>
    <div id="wrap">
        <div id="mlogin-container">
            <div class="mlogin-box">
                <router-link to="/">
                    <img class="con-logo" src="../../assets/images/logo.png" alt="Logo">
                </router-link>
                <div class="login-form">
                    <form @submit.prevent="mLogin">
                        <div class="form-group-login">
                            <input type="text" id="id" name="id" placeholder="" v-model="businessVo.bId">
                        </div>
                        <div class="form-group-login">
                            <input type="password" id="password" name="password" placeholder="" v-model="businessVo.bPw"
                                @keyup="checkCapsLock">
                            <span class="toggle-password" @click="togglePasswordVisibility">
                                <i :class="showPassword ? 'fas fa-eye' : 'fas fa-eye-slash'"></i>
                            </span>
                            <div v-if="isCapsLockOn" class="caps-lock-warning">CapsLock  !</div>
                        </div>
                        <button type="submit" class="loginBtn"></button>
                        <router-link to="/msignup">
                            <button class="signinBtn"></button>
                        </router-link>
                    </form>
                </div>
            </div><!-- login-box -->
            <div class="mlogin-image-container">
                <img src="../../assets/images/banner02.png" alt="Image">
            </div>
            <div class="mlogin-footer">
                <ul class="login-footer-ul">
                    <li></li>
                    <li></li>
                    <li>  </li>
                    <li> </li>
                </ul>
                <p> 2024  . All Rights Reserved.</p>
            </div>
        </div><!-- login-container -->
    </div><!-- wrap -->
</template>

<script>
import "@/assets/css/potal/mlogin.css"
import axios from 'axios';

export default {
    name: "ManagerLoginView",
    components: {
    },
    data() {
        return {
            id: "", //   
            password: "", //   
            showPassword: false, //   
            isCapsLockOn: false,
            businessVo:{
                bId: "",
                bPw: "",
            }, 
        };
    },
    methods: {
        togglePasswordVisibility() {
            this.showPassword = !this.showPassword; //  / 
            const passwordInput = document.getElementById('password');
            if (this.showPassword) {
                passwordInput.type = 'text'; //  
            } else {
                passwordInput.type = 'password'; //  
            }
        },
        checkCapsLock(event) {
            const isCapsLock = event.getModifierState && event.getModifierState('CapsLock');
            this.isCapsLockOn = isCapsLock;
        },
        mLogin() {
            console.log("");
            // const formData = new FormData();
            // formData.append("bId", this.id);
            // formData.append("bPw", this.password);
            // console.log(formData);
            // console.log(this.password);

            axios({
                method: 'post',
                url: `${this.$store.state.apiBaseUrl}/api/mlogin`,
                headers: { "Content-Type": "multipart/form-data" },
                data: this.businessVo,
                responseType: 'json' //   
            }).then(response => {
                console.log(response.data.apiData);
                // console.log(response.data.apiData.bNo);
                
                if(response.data.result == "success"){
                    let auth = response.data.apiData;

                    console.log(response.headers.authorization.split(" ")[1]);
                    const token = response.headers.authorization.split(" ")[1];

                    //vuex 
                    this.$store.commit("setAuth", auth);
                    this.$store.commit("setToken", token);

                    //   
                    if(auth.logo == null ){
                        this.$router.push('/editform');
                    }else{
                        this.$router.push(`/edit/${response.data.apiData.bNo}`);
                    }
                } else {
                    console.log(response.data.message);
                    alert("  .");
                }
            }).catch(error => {
                console.log(error);
                //  
            });
        }
    },
};
</script>
