<template>
    <!--  -->
    <div class="petAddFormModal" v-show="modalVisible">
        <div class="modal-content">
            <!--   -->
            <div class="modal-header">
                <h3></h3>
            </div>
            <!--   -->
            <div class="modal-body">
                <p>    .</p>
            </div>
            <!--   -->
            <div class="modal-footer">
                <button @click="closeModal"></button>
            </div>
        </div>
    </div>
    <div id="wrap">
        <AppHeader />
        <div id="petAddForm">
            <SideBar />
            <div class="container">
                <h1></h1>
                <!-- {{ this.$store.state.authUser.uNo }} -->
                <form v-on:submit.prevent="petUpdate" enctype="multipart/form-data">
                    <div class="petAddFormUnder">
                        <div class="filebox preview-image">
                            <img id="preview-image"  v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${dogVo.dogImg}`">
                            <label for="input-file"></label>
                            <input type="file" id="input-file" class="upload-hidden" @change="handleFileUpload">
                        </div>

                        <div class="detailBox">
                            <!--  -->
                            <div class="detail">
                                <label> </label>
                                <input type="text" placeholder="  ." v-model="dogVo.dogName">
                            </div>

                            <!--  -->
                            <div class="detail">
                                <div class="size">
                                    <span></span>
                                    <label for="small"></label><input id="small" type="radio" name="size" value=""
                                        v-model="dogVo.size">
                                    <label for="middle"></label><input id="middle" type="radio" name="size"
                                        value="" v-model="dogVo.size">
                                    <label for="big"></label><input id="big" type="radio" name="size" value=""
                                        v-model="dogVo.size">
                                </div>
                            </div>

                            <!--  -->
                            <div class="detail">
                                <label></label>
                                <input type="text" placeholder="  ." value="kind" v-model="dogVo.kind">
                            </div>

                            <!--  -->
                            <div class="detail">
                                <label class="title"></label>
                                <input type="number" value="weight" v-model="dogVo.weight" step="0.1"> kg
                            </div>

                            <!--  -->
                            <div class="detail" id="petAge">
                                <label></label>
                                <input type="date" value="birth" v-model="dogVo.birth" :max=now>
                            </div>

                            <!--  -->
                            <div class="detail">
                                <label></label>
                                <button type="button" :class="{ 'selected': dogVo.gender === 'male' }"
                                    @click="toggleDetail('gender', 'male', $event)"></button>
                                <button type="button" :class="{ 'selected': dogVo.gender === 'female' }"
                                    @click="toggleDetail('gender', 'female', $event)"></button>
                            </div>

                            <!-- {{ dogVo.neutering }} -->
                            <!--  -->
                            <div class="detail">
                                <!-- {{dogVo.neutering}} -->
                                <label></label>
                                <button type="button" :class="{ 'selected': dogVo.neutering == true }"
                                    @click="toggleDetail('neutering', true, $event)">O</button>
                                <button type="button" :class="{ 'selected': dogVo.neutering == false }"
                                    @click="toggleDetail('neutering', false, $event)">X</button>
                            </div>

                            <!--  -->
                            <div class="detail">
                                <!-- {{dogVo.experience}} -->
                                <label></label>
                                <button type="button" :class="{ 'selected': dogVo.experience == true }"
                                    @click="toggleDetail('experience', true, $event)">O</button>
                                <button type="button" :class="{ 'selected': dogVo.experience == false }"
                                    @click="toggleDetail('experience', false, $event)">X</button>
                            </div>

                            <!--  -->
                            <div class="detail" id="feature">
                                <label></label>
                                <button type="button" :class="{ 'selected': dogVo.skin }"
                                    @click="toggleFeature('skin')"></button>
                                <button type="button" :class="{ 'selected': dogVo.heart }"
                                    @click="toggleFeature('heart')"></button>
                                <button type="button" :class="{ 'selected': dogVo.marking }"
                                    @click="toggleFeature('marking')"></button>
                                <button type="button" :class="{ 'selected': dogVo.mounting }"
                                    @click="toggleFeature('mounting')"></button>

                                <div class="bite">
                                    <div class="biteRange">
                                        <label></label>
                                        <div class="biteRange2">
                                            <input type="range" min="1" max="3" step="1" list="tickmarks"
                                                v-model="dogVo.bite">
                                            <datalist id="tickmarks">
                                                <option value="1"></option>
                                                <option value="2"></option>
                                                <option value="3"></option>
                                            </datalist>
                                        </div>
                                    </div>
                                    <div class="pbox">
                                        <p></p>
                                        <p></p>
                                        <p></p>
                                    </div>
                                </div>
                                <textarea placeholder="  ." v-model="dogVo.memo"></textarea>
                            </div>
                        </div>
                    </div>
                    <!--  -->
                    <div>
                        <button class="insertBtn" type="submit"></button>
                    </div>
                </form>


            </div>
        </div>
        <TopButton />
        <AppFooter />
    </div><!-- wrap -->
</template>

<script>
import AppFooter from "@/components/AppFooter.vue"
import AppHeader from "@/components/AppHeader.vue"
import TopButton from "@/components/TopButton.vue";
import SideBar from "@/components/SideBar.vue";

import "@/assets/css/mypage/mypage.css"

import axios from 'axios';

export default {
    name: "PetUpdateForm",
    props: ['dogNo'],
    components: {
        TopButton,
        AppHeader,
        AppFooter,
        SideBar,
    },

    data() {
        return {
            modalVisible: false,     //     
            dogVo: {
                dogNo: this.dogNo,
                dogName: "",
                kind: "",
                weight: 0,
                birth: "",
                gender: null,
                neutering: false,
                experience: false,
                memo: "",
                file: "",
                size: "",
                skin: false,
                heart: false,
                marking: false,
                mounting: false,
                bite: 0,
                uNo: this.$store.state.authUser.uNo,  //
                dogImg:"",
            },
            now: new Date().toISOString().substr(0, 10),    // 

        };
    },
    methods: {

        handleFileUpload(event) {
            const file = event.target.files[0];
            const reader = new FileReader();

            if (!file.type.match(/image\//)) {
                this.modalVisible = true;
                return;
            }

            reader.onload = () => {
                const imgSrc = reader.result;
                //      
                //    
                const imgElement = document.getElementById('preview-image'); //   <img>  
                imgElement.src = imgSrc; //     
            };

            reader.readAsDataURL(file);

            // FormData  
            this.dogVo.file = file;

            console.log(this.dogVo.file);
        },


        //   
        showModal() {
            this.modalVisible = true;
        },

        //   
        closeModal() {
            this.modalVisible = false;
        },



        //  
        toggleDetail(type, value, event) {
            const buttons = event.target.parentElement.querySelectorAll('button');
            buttons.forEach(btn => {
                btn.classList.toggle('selected', btn === event.target);
            });
            this.dogVo[type] = (this.dogVo[type] === value) ? null : value;
        },

        //  true/false
        toggleFeature(feature) {
            this.dogVo[feature] = !this.dogVo[feature];
        },


        //   
        petGetInfor() {
            console.log(" ");
            console.log(this.dogNo);

            axios({
                method: 'get',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/api/mypage/petgetinfor`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { dogNo: this.dogNo }, //get   

                responseType: 'json' //
            }).then(response => {
                this.dogVo = response.data.apiData;
                console.log(this.dogVo);

            }).catch(error => {
                console.log(error);
            });

        },

        //  
        petUpdate() {
            console.log(" ");
            console.log(this.dogVo.dogNo);
            console.log(this.dogVo);

            let formData = new FormData();
            for (let key in this.dogVo) {
                formData.append(key, this.dogVo[key]);
            }


            axios({
                method: 'put', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/mypage/petupdate`,
                headers: { "Content-Type": "multipart/form-data" },
                data: formData, //put, post, delete   JSON  
                responseType: 'json' //

            }).then(response => {
                console.log(response.data);

                if (response.data.result == "success") {
                    this.$router.push(`/mypage/${this.uNo}`);

                } else {
                    alert(" ");
                }

            }).catch(error => {
                console.log(error);
            });

        }


    },
    created() {
        this.petGetInfor();
    }
};
</script>




<style>
/*  */
#petAddForm .selected {
    background-color: #236C3F !important;
    color: #ffffff !important;
}


#petAddForm input[type="number"]::-webkit-outer-spin-button,
#petAddForm input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}


/*    */
.petAddFormModal .modal-content {
    position: absolute;
    left: 40%;
    top: 30%;
    z-index: 999;
    width: 400px;
    border: 1px solid black;
    border-radius: 10px;
    background-color: white;
    height: 150px;

}


/*    */
.petAddFormModal .modal-header {
    background-color: #236C3F;
    color: white;
    width: 400px;
    text-align: center;
    padding: 8px 0 8px 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;

}

.petAddFormModal .modal-body {
    padding: 10px 0 0 20px;
    font-size: 18px;
}

/*    */
.petAddFormModal .modal-footer {
    padding: 2px 16px;
    /* background-color: #5cb85c; */
    color: white;
    margin-top: 30px;
}

.petAddFormModal .modal-footer button {
    float: right;
    padding: 5px;
    border: none;
    margin-bottom: 10px;
    width: 100px;
    border-radius: 10px;


}

.petAddFormModal .close:hover,
.petAddFormModal .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}
</style>
