<template>
    <div>
        <ManagerHeader /> <!--    -->

        <div class="calendar-container">
            <FullCalendar ref="calendar" :options="calendarOptions" id="calendar" />
        </div>

        <ManagerFooter /> <!--    -->
        <!--   -->
        <div v-if="selectedEvent" class="schedule-modal" @click.self="closeModal">
            <div class="schedule-modal-content">
                <h2> <br><br>{{ selectedEvent.title }}</h2>
                <p>: {{ selectedEvent.start }}</p>

                <!--     -->
                <p>: {{ selectedEvent.extendedProps.petName }}</p>
                <p>: {{ selectedEvent.extendedProps.breed }}</p>
                <p>: {{ selectedEvent.extendedProps.groomingStyle }}</p>
                <p>: {{ selectedEvent.extendedProps.price }}</p>
                <span class="schedule-edit" @click="editEvent"></span>
                <span class="schedule-delete" @click="deleteEvent"></span>
                <span class="schedule-close" @click="closeModal"></span>
            </div>
        </div>
    </div>
</template>

<script>
import ManagerFooter from "@/components/ManagerFooter.vue";
import ManagerHeader from "@/components/ManagerHeader.vue";
import FullCalendar from "@fullcalendar/vue3";
import dayGridPlugin from "@fullcalendar/daygrid";
import Swal from "sweetalert2";
import interactionPlugin from "@fullcalendar/interaction"; //     
import { mapGetters, mapMutations } from 'vuex'; // Vuex    mapMutations 
import "@/assets/css/manager/schedule.css"; //    CSS 
import axios from 'axios';

export default {
    name: "ScheduleView", //  

    components: {
        ManagerFooter,
        ManagerHeader,
        FullCalendar
    },
    data() {
        return {
            showModal: false, //   
            selectedEvent: null, //   
            calendarOptions: {

                plugins: [dayGridPlugin, interactionPlugin], // interactionPlugin 
                initialView: "dayGridMonth",
                headerToolbar: {
                    start: "prev,next today",
                    center: "title",
                    end: "dayGridMonth,dayGridWeek,dayGridDay"
                },
                editable: true, //       
                contentHeight: 800,
                locale: "ko",
                events: [],
                eventClick: this.handleEventClick,
                eventDrop: this.handleEventDrop, //    
                eventResize: this.handleEventResize //     
            },
            availableTimes: [] //     
        };
    },
    computed: {
        ...mapGetters(['reservationData']) // Vuex reservationData   
    },
    mounted() {
        this.$store.commit('setReservationData', this.reservations);
        const bNo = this.$route.params.bNo || 1; //     ,   1
        this.fetchReserveList(bNo); // bNo    
    },
    methods: {
        ...mapMutations(['setSelectedSchedule', 'setGroomingRecord']), // Vuex  

        //--------------------       ----------------------------

        fetchReserveList(bNo) {
            axios({
                method: 'get',
                url: `${this.$store.state.apiBaseUrl}/api/jw/${bNo}`,
                headers: { "Content-Type": "application/json; charset=utf-8" },
                responseType: 'json'
            }).then(response => {
                //     
                console.log(' :', response.data.apiData); //  
                this.reservations = Array.isArray(response.data.apiData) ? response.data.apiData : [];
                this.$store.commit('setReservationData', this.reservations); //   
                this.updateCalendarEvents(bNo); //     
            }).catch(error => {
                //  
                console.error('Error fetching reservations:', error);
            });
        },

        updateCalendarEvents(bNo) {
            const events = this.reservationData.map(reservation => {
                const dateParts = reservation.rtDate.split('-'); // rtDate , ,  
                const timeParts = reservation.rtTime.split(':'); // rtTime   

                // , , , ,    Date  
                const startTime = new Date(
                    parseInt(dateParts[0]),  // 
                    parseInt(dateParts[1]) - 1, //  (0  1 )
                    parseInt(dateParts[2]), // 
                    parseInt(timeParts[0]), // 
                    parseInt(timeParts[1]) // 
                );

                //       
                const backgroundColor = '#FFFFFF';//      

                return {
                    title: `${reservation.dogName}, ${reservation.beauty}, ${reservation.kind}, ${reservation.expectedPrice}`,
                    start: startTime,
                    backgroundColor,
                    extendedProps: {
                        petName: reservation.dogName,
                        breed: reservation.kind,
                        groomingStyle: reservation.beauty,
                        price: reservation.expectedPrice,
                        rsNo: reservation.rsNo,
                        bNo: bNo // bNo  
                    }
                };
            });

            this.calendarOptions.events = events;
            this.$refs.calendar.getApi().refetchEvents();
        },

        handleEventDrop(info) {
            console.log("handleEventDrop");

            //      
            this.selectedEvent = info.event;
            this.showModal = true;

            //     
            const rsNo = info.event.extendedProps.rsNo;

            //      
            this.selectedEvent.setStart(info.event.start);
            this.selectedEvent.setEnd(info.event.end);

            //      API 
            this.updateEventOnServer(rsNo, info.event);

            //        
            this.addAvailableTime(info.event.start, info.event.end);
        },

        addAvailableTime(startTime, endTime) {
            //        ,     .
            const newAvailableTimes = [];

            this.availableTimes.forEach(time => {
                //        .
                if (time.start.getTime() < startTime.getTime() || time.end.getTime() > endTime.getTime()) {
                    newAvailableTimes.push(time);
                }
            });
            this.availableTimes = newAvailableTimes;
        },


        //--------------------   ----------------------------
        updateEventOnServer(rsNo, event) {
            console.log("updateEventOnServer");
            const start = event.start.toISOString().slice(0, 19).replace('T', ' '); // ISO 8601  MySQL  

            //      API 
            axios({
                method: 'put',
                url: `${this.$store.state.apiBaseUrl}/api/jw/${rsNo}/date`,
                data: { rsNo: rsNo, rtDate: start }, //  
                headers: { "Content-Type": "application/json; charset=utf-8" },
                responseType: 'json'
            }).then(response => {
                console.log(response.data.apiData); //
                //    
                console.log('  .');
                Swal.fire(' ', '  .', 'success');
            }).catch(error => {
                //  
                console.error('Error updating event:', error);
                Swal.fire('  ', '    .', 'error');
            });
        },

        editEvent() {
            Swal.fire({
                title: ' ',
                html: `
            <input id="editStart" class="swal2-input" type="datetime-local" placeholder=" " value="${this.formatDateTimeLocal(this.selectedEvent.start)}">
            <input id="editTitle" class="swal2-input" placeholder="" value="${this.selectedEvent.title}">
        `,
                focusConfirm: false,
                preConfirm: () => {
                    const start = Swal.getPopup().querySelector('#editStart').value;
                    const title = Swal.getPopup().querySelector('#editTitle').value;

                    if (!title || !start) {
                        Swal.showValidationMessage('    .');
                    }

                    return { title, start };
                }
            }).then(result => {
                if (result.isConfirmed) {
                    const { start, title } = result.value;
                    //  this.selectedEvent.setStart(new Date(start));
                    this.selectedEvent.setProp('title', title);

                    //      API 
                    this.updateEventTimeOnServer(this.selectedEvent.extendedProps.rsNo, start);
                }
            });
        },


        //--------------------   ----------------------------
        updateEventTimeOnServer(rsNo, start) {
            function convertToMySQLTime(isoTime) {
                const date = new Date(isoTime);
                const hours = date.getHours().toString().padStart(2, '0');
                const minutes = date.getMinutes().toString().padStart(2, '0');
                const seconds = date.getSeconds().toString().padStart(2, '0');
                return `${hours}:${minutes}:${seconds}`;
            }

            const startTime = convertToMySQLTime(start);

            axios({
                method: 'put',
                url: `${this.$store.state.apiBaseUrl}/api/jw/${rsNo}/time`,
                data: { rsNo: rsNo, rtTime: startTime },
                headers: { "Content-Type": "application/json; charset=utf-8" },
                responseType: 'json'
            }).then(() => {
                console.log('  .');
                Swal.fire(' ', '  .', 'success');
            }).catch(error => {
                console.error('  :', error);
                Swal.fire('  ', '    .', 'error');
            });
        },


        formatDateTimeLocal(date) {
            const d = new Date(Date.parse(date)); // 
            const year = d.getFullYear();
            const month = (d.getMonth() + 1).toString().padStart(2, '0');
            const day = d.getDate().toString().padStart(2, '0');
            const hours = d.getHours().toString().padStart(2, '0');
            const minutes = d.getMinutes().toString().padStart(2, '0');
            return `${year}-${month}-${day}T${hours}:${minutes}`;
        },


        //--------------------      ----------------------------

        deleteEvent() {
            Swal.fire({
                title: ' ',
                text: '   ?',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#d33',
                cancelButtonColor: '#3085d6',
                confirmButtonText: '',
                cancelButtonText: ''
            }).then(result => {
                if (result.isConfirmed) {
                    const rsNo = this.selectedEvent.extendedProps.rsNo;

                    if (!rsNo) {
                        console.error('rsNo  .');
                        Swal.fire(' ', '    .', 'error');
                        return;
                    }

                    this.selectedEvent.remove();

                    //     API 
                    this.deleteEventOnServer(rsNo);

                    //    ,      .
                    //       .
                    this.availableTimes.push({
                        start: this.selectedEvent.start,
                        end: this.selectedEvent.end
                    });

                    this.showModal = false;
                    Swal.fire(' ', ' .', 'success');
                }
            });
        },

        deleteEventOnServer(rsNo) {
            axios({
                method: 'delete',
                url: `${this.$store.state.apiBaseUrl}/api/jw/${rsNo}/delete`,
                headers: { "Content-Type": "application/json; charset=utf-8" },
                responseType: 'json'
            }).then(response => {
                console.log(response.data.apiData); //   
                console.log('  .');
            }).catch(error => {
                console.error('Error deleting event:', error);
                Swal.fire('  ', '    .', 'error');
            });
        },

        // ------------------ ----------------------------

        handleEventClick(info) {
            if (!info || !info.event) {
                console.error('   .');

                return;
            }

            const event = info.event;
            if (!event.extendedProps || event.extendedProps.sent) {
                return; //     
            }
            console.log('Event :', event);

            const eventTitle = event.title || '';
            const eventStart = event.start ? new Date(event.start).toLocaleString().substring(0, 20).replace("/g", "") : '';

            Swal.fire({
                title: "",
                html: ": " + eventTitle + "<br/>: " + eventStart,
            });

            this.$store.commit("setSelectedSchedule", event);

            this.$router.push({ name: 'diary' }).then(() => {
                if (!event.extendedProps) {
                    console.error('event.extendedProps .');
                    return;
                }

                const rsNo = event.extendedProps.rsNo;
                const bNo = event.extendedProps.bNo;


                if (!rsNo) {
                    console.error('event.extendedProps.rsNo  .');
                    return;
                }
                console.log('  (rsNo):', rsNo);
                console.log('bNo :', bNo);



                // rsNo bNo   
                this.$root.$emit('selectGroomingRecord', { rsNo, bNo });
            });
        },
        //       sent  true   
        updateReservationSentStatus(rsNo) {
            const reservation = this.reservations.find(res => res.rsNo === rsNo);
            if (reservation) {
                reservation.sent = true;
            }
        },


        closeModal() {
            this.selectedEvent = null;
            this.showModal = false;
        },



    }
};
</script>