<template>
    <div class="sidebar" :style="sidebarStyle">
        <!--   -->
        <div class="profile">
            <!--   -->
            <!-- {{ userVo.uProfile }} -->
            <div class="profileImg">
                <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${userVo.uProfile}`">
            </div>
            <!--   -->
            <div class="profileId" v-if="userVo">
                <label>{{ userVo.uName }}</label>  <br>
                <label> : {{ userVo.uPoint.toLocaleString() }}</label> P
            </div>
        </div>

        <!--  -->
        <div class="profilePetBox">

            <!--    -->
            <div class="profileTitle">
                <p style="width: 50px;"></p>
                <router-link to="/petaddform"><img src="@/assets/images/add_icon_143118.png" style="width: 24px;"></router-link>
            </div>

            <!--    -->
            <div class="petProfileBox" v-bind:key="i" v-for="(dogVo, i) in dogList">
                <!--  1 -->
                <div class="profilePet" v-if="dogVo">

                    <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${dogVo.dogImg}`">
                    <p>{{ dogVo.dogName }} <router-link :to="`/petupdateform/${dogVo.dogNo}`"> 
                        <label style="cursor: pointer;"></label></router-link></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import '@/assets/css/mypage/sidebar.css'
export default {
    name: "SideBar",
    components: {
    },

    data() {
        return {
            isFixed: false,
            userVo: {
                uProfile: "",
                uNo: this.$store.state.authUser.uNo,
                uId: "",
                uPoint: 0,
                uName:this.$store.state.authUser.uName
            },
            dogVo: {
                dogName: "",
                dogImg: "",
                dogNo: 0,
            },
            dogList: [],

        };
    },
    computed: {
        //   
        sidebarStyle() {
            return {
                position: this.isFixed ? 'fixed' : 'fixed',
                top: this.isFixed ? '0' : '138px',
                transition: 'top 0.3s ease',
            };
        },
    },
    mounted() {
        window.addEventListener('scroll', this.handleScroll);
    },
    beforeUnmount() {
        window.removeEventListener('scroll', this.handleScroll); //     
    },
    methods: {
        //  
        handleScroll() {
            const scrollTop = window.scrollY || window.pageYOffset; //   
            if (scrollTop > 138) {
                //   138px 
                this.isFixed = true; //    
            } else {
                this.isFixed = false; //     
            }
        },

        getUserInfor() {
            console.log(" ");
            console.log(this.userVo.uNo);

            axios({
                method: 'get', // put, post, delete                   
                url: `${this.$store.state.apiBaseUrl}/api/mypage/sidebar`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { uNo: this.userVo.uNo }, //get   
                // data: guestbookVo, //put, post, delete   JSON  

                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //
                this.userVo = response.data.apiData;
                console.log(this.userVo);

                this.getUserInfor2();

            }).catch(error => {
                console.log(error);
            });

        },

        getUserInfor2() {
            console.log(" ");
            console.log(this.userVo.uNo);

            axios({
                method: 'get', // put, post, delete                   
                url: `${this.$store.state.apiBaseUrl}/api/mypage/sidebar2`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { uNo: this.userVo.uNo }, //get   
                // data: guestbookVo, //put, post, delete   JSON  

                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //
                this.dogList = response.data.apiData;

                console.log(this.dogList);

            }).catch(error => {
                console.log(error);
            });

        }
    },
    created() {
        this.getUserInfor();
    }
};
</script>

<style></style>
