    methods: {
        // 
        getShopInfo(){
            console.log("  ");

            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/su/shopInfo`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {bNo: this.bNo}, //get   
                // data: this.userVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {

                this.shopInfo = response.data.apiData.shopInfo;
             

            }).catch(error => {
                console.log(error);
            });
        }
    },
    created() { 
        this.getShopInfo()
    }
};
