<template>
    <div>
        <!--  -->
        <router-link to="#" class="toReserveFrom" v-on:click="goReserve" :class="{ 'show': showButton }">
            <!-- :class() :showButton true show  flase show  -->
            <div class="goReForm"><br></div>
        </router-link>
    </div>
</template>
<script>
export default {
    name: "TopButton",
    components: {},
    data() {
        return {
            showButton:false
        };
    },
    methods: {
        scrollToTop(){
            // console.log(" ")
            window.scrollTo({
                top:0,
                behavior:"smooth"
            });
        },
        handleScroll(){
            //    //    2,     . 2    ()
            const scrollPosition = window.pageYOffset || document.documentElement.scrollTop; 

            //     
            if(scrollPosition > 200){
                this.fadeIn();
            }else{
                this.fadeOut();
            }
        },
        fadeIn() {
            const reserveButton  = document.querySelector('.toReserveFrom');
            reserveButton .style.transition = 'opacity 0.3s ease';
            reserveButton .style.opacity = '1';
        },
        fadeOut() {
            const reserveButton  = document.querySelector('.toReserveFrom');
            reserveButton .style.transition = 'opacity 0.3s ease';
            reserveButton .style.opacity = '0';
        }
        
    },
    created(){

    },
    mounted(){
        this.handleScroll(); //      
        window.addEventListener("scroll", this.handleScroll);
    },
    unmounted() {
        //       
        window.removeEventListener("scroll", this.handleScroll);
    }
};
</script>
