<script setup>
import { KakaoMap, KakaoMapMarker } from 'vue3-kakao-maps';
import { defineComponent} from "vue";
import "vue3-carousel/dist/carousel.css";
//  setup   setup  import reactive   .
import { reactive, onMounted } from 'vue';
//Composition API setup    this     $store  useStore     
import { useStore } from 'vuex';
// import { useRoute } from 'vue-router';

    // 
    const store = useStore();

    //  
    const coordinate = reactive({
        lat: 0,
        lng: 0
    });

    //      
    const getLatLng = () => {
        axios({
            method: 'get',
            url: `${store.state.apiBaseUrl}/api/su/shopInfo`, //store 
            headers: { "Content-Type": "application/json; charset=utf-8" },
            params: { bNo: store.state.auth.bNo },
            responseType: 'json'
        }).then(response => {
            // shopInfo      coordinate  .
            coordinate.lat = response.data.apiData.shopInfo.latitude;
            coordinate.lng = response.data.apiData.shopInfo.longitude;
        }).catch(error => {
            console.log(error);
        });

    };
    //      
    onMounted(() => {
        getLatLng();
    });
</script>
<script>
   import '@/assets/css/edit/editform.css'
   import ManagerFooter from "@/components/ManagerFooter.vue"
   import ManagerHeader from "@/components/ManagerHeader.vue"
   import TopButton from "@/components/TopButton.vue"
   import axios from 'axios';
   
    export default defineComponent({
        name: "EditView",
        components: {
                ManagerFooter,
                ManagerHeader,
                TopButton
        },
        data() {
            return{
                bNo: this.$store.state.auth.bNo,
                shopInfo: {
                    bZipCode: "",
                    bAddress: "",
                    bdAddress: "",
                    bPhone: "",
                    title: "", 
                    subTitle: "",
                    logoFile: null, // 
                    logo:"", // savaName
                    utilTime: "",
                    dName:"",
                    introduce:"",
                    dProfileFile:null,// 
                    dProfile:"",// saveName
                    job:"",
                    slideImgs:[],// 
                    cutImgs:[],// 
                    hiNo:""
                },
                priceList: this.initializePriceList(),
                slideList:[],// saveName List + hiNo
                cutList:[],// saveName List   
                delSlideHiNos:[], //   
                delCutHiNos:[], //   

                action: ''

            }
        },
        methods: {

            // !  !
            initializePriceList() {
                let priceList = [];
                for (let i = 1; i < 66; i++) { // beautyNo 1   1  
                    priceList.push({ beautyNo: i ,onePrice: '' });
                }
                return priceList;
            },
            // ,  
            setAction(actionType) {
                this.action = actionType;
            },
            handleSubmit() {
                if (this.action === 'add') {
                    this.addShopInfo();
                } else if (this.action === 'update') {
                    this.updateShopInfo();
                }
            },
            
            // ==========   ==========
            addShopInfo(){
                console.log("  ");

                if (!this.shopInfo.logoFile) {
                    alert("  .");
                    return;
                }

                if (!this.shopInfo.title) {
                    alert("  .");
                    return;
                }

                if (!this.shopInfo.subTitle) {
                    alert("  .");
                    return;
                }

                // if (this.shopInfo.slideImgs.length === 0) {
                //     alert("      .");
                //     return;
                // }

                // if (this.shopInfo.cutImgs.length === 0) {
                //     alert("      .");
                //     return;
                // }

                if (!this.shopInfo.dName) {
                    alert("  .");
                    return;
                }

                if (!this.shopInfo.job) {
                    alert("  .");
                    return;
                }

                if (!this.shopInfo.introduce) {
                    alert("  .");
                    return;
                }

                if (!this.shopInfo.dProfileFile) {
                    alert("   .");
                    return;
                }

                for (let i = 0; i < this.priceList.length; i++) {
                    if (!this.priceList[i].onePrice) {
                        alert(`   ${i+1}  .`);
                        return;
                    }
                    if (!this.priceList[i].beautyNo) {
                        alert(`   ${i+1}   .`);
                        return;
                    }
                }

                let formData = new FormData();

                formData.append("bNo", this.bNo);

                formData.append("logoFile", this.shopInfo.logoFile);
                formData.append("title", this.shopInfo.title);
                formData.append("subTitle", this.shopInfo.subTitle);
                
                this.shopInfo.slideImgs.forEach((img, index) => {
                    formData.append(`slideImgs[${index}]`, img);
                });
                this.shopInfo.cutImgs.forEach((img, index) => {
                    formData.append(`cutImgs[${index}]`, img);
                });

                formData.append("dName", this.shopInfo.dName);
                formData.append("job", this.shopInfo.job);
                formData.append("introduce", this.shopInfo.introduce);
                formData.append("dProfileFile", this.shopInfo.dProfileFile);

                // priceList    
                for (let i = 0; i < this.priceList.length; i++) {
                    formData.append(`priceList[${i}].onePrice`, this.priceList[i].onePrice);
                    formData.append(`priceList[${i}].beautyNo`, this.priceList[i].beautyNo);
                    
                }

                console.log("=====   =====");

                console.log(formData.get("logoFile"));
                console.log(formData.get("subTitle"));

                // console.log(formData.get("slideImgs"));
                // console.log(formData.get("cutImgs"));

                console.log(formData.get("dName"));
                console.log(formData.get("job"));
                console.log(formData.get("introduce"));

                console.log(formData.get("dProfileFile"));

                // console.log("   ");
                // priceList   
                // for (let i = 0; i < this.priceList.length; i++) {
                //     console.log(`priceList[${i}]`, formData.get(`priceList[${i}].beautyNo`),formData.get(`priceList[${i}].onePrice`));
                // }

                axios({
                    
                    method: 'post', // put, post, delete 
                    url: `${this.$store.state.apiBaseUrl}/api/su/registerShop`,
                    headers: { "Content-Type": "multipart/form-data" }, //
                    // params: {bNo: this.bNo}, //get   
                    data: formData, // formData   Json  -> springboot  modelattribute (formData !)
                    responseType: 'json' //
                }).then(response => {
                    console.log(response.data.apiData);


                    // if (response.data.result == "success") {
                    //     alert("  .");
                    //     this.$router.push(`/edit/${this.bNo}`);

                    // } else {
                    //     alert("  .");
                    // }
                    

                }).catch(error => {
                    console.log(error);
                });
            },

            // ==========   ==========
            getShopInfo(){
                // console.log(this.bNo);
                axios({
                    method: 'get', // put, post, delete 
                    url: `${this.$store.state.apiBaseUrl}/api/su/shopInfo`,
                    headers: { "Content-Type": "application/json; charset=utf-8" }, //
                    params: {bNo: this.bNo}, //get   
                    // data: this.userVo, //put, post, delete   JSON  
                    responseType: 'json' //
                }).then(response => {

                    this.shopInfo = response.data.apiData.shopInfo||{} ; //title  
                   
                    this.priceList = response.data.apiData.pList.length ? response.data.apiData.pList :this.initializePriceList()  ;
                    this.slideList = response.data.apiData.sList;
                    // console.log(this.slideList);
                    this.cutList = response.data.apiData.cList;
                    

                    //   
                    if (this.shopInfo.logo) {
                        this.logoImagsView('att_zone4', 'btnAtt4');
                    }

                    //   
                    if (this.slideList) {
                        this.slideImgsView('att_zone', 'btnAtt');
                        console.log(" ");
                    }

                    //   
                    if (this.cutList) {
                        this.CutimgsView('att_zone2', 'btnAtt2');
                    }

                    //   
                    if (this.shopInfo.dProfile) {
                        this.DimgsView('att_zone3', 'btnAtt3');
                    }

                }).catch(error => {
                    console.log(error);
                });
            },
            // ==========   ==========
            updateShopInfo(){
                console.log("  ");
                let formData = new FormData();

                formData.append("bNo", this.bNo);

                formData.append("title", this.shopInfo.title);
                formData.append("subTitle", this.shopInfo.subTitle);
                
                formData.append("dName", this.shopInfo.dName);
                formData.append("job", this.shopInfo.job);
                formData.append("introduce", this.shopInfo.introduce);

                // **********    **********
                //   .          . 
                if(this.shopInfo.logoFile !=null){
                    formData.append("logoFile", this.shopInfo.logoFile);
                }

                if(this.shopInfo.dProfileFile!=null){
                    formData.append("dProfileFile", this.shopInfo.dProfileFile);
                }

                // **********    **********
                //      
                if (!this.delSlideHiNos) {
                    this.delSlideHiNos = [];
                }

                //      
                if (!this.shopInfo.slideImgs) {
                    this.shopInfo.slideImgs = [];
                }

                //      
                if (!this.shopInfo.cutImgs) {
                    this.shopInfo.cutImgs = [];
                }

                //    
                if(this.delSlideHiNos.length ==0 ){
                    console.log("   ");
                }else{
                    this.delSlideHiNos.forEach(hiNo => {
                        formData.append('delSlideHiNos[]', hiNo);
                    });
                    console.log("  ");  
                }
                
                //   
                if(this.shopInfo.slideImgs.length != 0){
                    this.shopInfo.slideImgs.forEach((img, index) => {
                        formData.append(`slideImgs[${index}]`, img);
                    });
                    console.log(" ");
                    console.log(this.shopInfo.slideImgs);
                }else{
                    console.log("  ");
                }

                //    
                if(this.delCutHiNos.length ==0 ){
                    console.log("   ");
                }else{
                    this.delCutHiNos.forEach(hiNo => {
                        formData.append('delCutHiNos[]', hiNo);
                    });
                    console.log("  ");  
                }

                //    
                if(this.shopInfo.cutImgs.length != 0){
                    
                    this.shopInfo.cutImgs.forEach((img, index) => {
                        formData.append(`cutImgs[${index}]`, img);
                    });
                    console.log(" ");
                    console.log(this.shopInfo.cutImgs);
                }else{
                    console.log("  ");
                }
                
               

                // ********** priceList     **********
