            // ==========  1 ==========
            logoImagsView(att_zone4, btn) {
                var self = this;
                var attZone = document.getElementById(att_zone4);
                var btnAtt4 = document.getElementById(btn);

                //     div 
                var div_style = 'display:inline-block;position:relative;width:125px;height:125px;border:1px solid #a7a4a4;z-index:1';

                //   
                var img_style = 'width:100%;height:100%;z-index:none;object-fit:contain';

                //    
                btnAtt4.onchange = function (e) {
                    var file = e.target.files[0];
                    
                    if (!file.type.match(/image.*/)) {
                        alert("   .");
                        return;
                    }
                    
                    imageLoader(file);
                    updateFileInput(file);
                };

                //     
                attZone.addEventListener('dragenter', function (e) {
                    e.preventDefault();
                    e.stopPropagation();
                    console.log("")
                }, false);

                attZone.addEventListener('dragover', function (e) {
                    e.preventDefault();
                    e.stopPropagation();
                }, false);

                attZone.addEventListener('drop', function (e) {
                    e.preventDefault();
                    e.stopPropagation();
                    var dt = e.dataTransfer;
                    var file = dt.files[0];
                    if (!file.type.match(/image.*/)) {
                        alert("   .");
                        return;
                    }
                    
                    imageLoader(file);
                    updateFileInput(file);
                }, false);


                //   
                function imageLoader(file) {
                    var reader = new FileReader();

                    reader.onload = function (ee) {
                        let img = document.createElement('img');
                        img.setAttribute('style', img_style);
                        img.src = ee.target.result;
                        attZone.innerHTML = '';//   
                        attZone.appendChild(makeDiv(img));// div
                        attZone.classList.add('file-attached'); //      ->  css   
                        
                        self.shopInfo.logoFile = file;//  .
                    };

                    reader.readAsDataURL(file);
                }

                //       
                function makeDiv(img) {
                    var div = document.createElement('div');
                    div.setAttribute('style', div_style);

                    div.appendChild(img);
                    return div;
                }

                //     
                function updateFileInput(file) {
                    var dt = new DataTransfer();
                    dt.items.add(file);
                    btnAtt4.files = dt.files;
                }

                //   
                if (self.shopInfo.logo) {
                    let img = document.createElement('img');
                    img.src = `${self.$store.state.apiBaseUrl}/upload/${self.shopInfo.logo}`;
                    img.style.width = '100%';
                    img.style.height = '100%';
                    img.style.objectFit = 'contain';
                    
                    attZone.innerHTML = ''; //   
                    attZone.appendChild(img); //   
                    attZone.classList.add('file-attached'); //     

                    //       
                    var initialFile = new File([], self.shopInfo.logo); //      
                    updateFileInput(initialFile);

                    //     FormData 
                    //      
                    // fetch(img.src)
                    // .then(response => response.blob())
                    // .then(blob => {
                    //     const initialFile = new File([blob], self.shopInfo.logo, { type: blob.type });
                    //     updateFileInput(initialFile);
                    //     self.shopInfo.logoFile = initialFile; //   
                    // })
                    // .catch(error => console.error('Error fetching the image:', error));

                    

                }

            },
