                    e.preventDefault();
                    e.stopPropagation();
                    console.log("18")
                }, false);

                attZone.addEventListener('drop', function (e) {
                    // var files = {};
                    e.preventDefault();
                    e.stopPropagation();
                    var dt = e.dataTransfer;
                    var files = dt.files;
                    if (sel_files.length < 18) {
                        for (let f of files) {
                            if (sel_files.length < 18) {
                                imageLoader(f);
                            } else if (!alerted) {
                                alert(" 18   .");
                                alerted = true;
                                break;
                            }
                        }
                    } else if (!alerted) {
                        alert(" 18   .");
                        alerted = true;
                    }
                    updateFileInput();
                }, false);

                //   -    
                function imageLoader(file) {
                    if (sel_files.length >= 18) { //  18    
                        return;
                    }
                    sel_files.push(file);
                    var reader = new FileReader();
                    reader.onload = function (ee) {
                        let img = document.createElement('img');
                        img.setAttribute('style', img_style);
                        img.src = ee.target.result;
                        attZone.appendChild(makeDiv(img, file));
                    };
                    reader.readAsDataURL(file);

                    self.shopInfo.cutImgs = sel_files;
                }

                //      checkbox  attZone  div  
                function makeDiv(img, file) {
                    var div = document.createElement('div');
                    div.setAttribute('style', div_style);

                    var btn = document.createElement('input');
                    btn.setAttribute('type', 'button');
                    btn.setAttribute('value', 'x');
                    btn.setAttribute('delFile', file.saveName);
                    btn.setAttribute('hino', file.hiNo);
                    // console.log("***")
                    // console.log(file)
                    // console.log(file.hiNo)
                    btn.setAttribute('style', chk_style);

                    //
                    btn.onclick = function (ev) {
                        var ele = ev.srcElement;
                        var delFile = ele.getAttribute('delFile');
                        var hiNo = ele.getAttribute('hino'); // hiNo  

                        //    hiNo  .
                        if(hiNo =='undefined'){//  
                            console.log("hiNo == undefined");
                            console.log(hiNo);
                        }else{
                            self.delCutHiNos.push(hiNo);
                            console.log(self.delCutHiNos);
                        }
                        

                         //    
                        for (var i = 0; i < sel_files.length; i++) {
                            if (delFile == sel_files[i].name) {
                                sel_files.splice(i, 1);
                            }
                        }

                        updateFileInput();

                        var p = ele.parentNode;
                        attZone.removeChild(p);
                    };
                    div.appendChild(img);
                    div.appendChild(btn);
                    return div;
                }

                 //     
                 function updateFileInput() {
                    var dt = new DataTransfer();
                    sel_files.forEach(file => {
                        dt.items.add(file);
                    });
                    btnAtt2.files = dt.files;
                }

                //   
                if (self.cutList && self.cutList.length > 0) {

                    // var initialFiles = [];

                    self.cutList.forEach(slide => {
                        let img = document.createElement('img');
                        img.setAttribute('style', img_style);
                        img.src = `${this.$store.state.apiBaseUrl}/upload/${slide.saveName}`;
                        img.setAttribute('hiNo', slide.hiNo );
                        // console.log(slide.hiNo );
                        // console.log(slide );
                        attZone.appendChild(makeDiv(img, slide));
                        // initialFiles.push(new File([], slide)); // Placeholder file for the initial files
                    });
                    // sel_files = initialFiles;
                    // updateFileInput();
                }

                //  alert    
                var alerted = false;
            },
