<template>
    <div class="header">
        <div class="headerContainer">
            <!--  -->
            <router-link to="/">
                <img class="logo" src="../assets/images/logo.png">
            </router-link>
            <!--  -->
            <div class="head">
                <!--   -->
                <ul class="categories">
                    <li><router-link to="/searchmap"></router-link></li>
                    <li><router-link to="/search"></router-link></li>
                    <li><router-link to="/announcements"></router-link></li>
                    <li><router-link to="/reservationform"></router-link></li>
                </ul>

                <!-- <div class="hot">
                    <img src="../assets/images/hot.png">
                </div> -->
                <!-- <div class="popRank" v-if="popList.length > 0">
                    <ul id="best_search">
                        <li v-for="(popVo, i) in popList" :key="i">
                            <dl :class="'time' + (i + 1)" v-show="i === viewcount">
                                <dd>
                                    <a class="t" href="`/edit/${popVo.bNo}`">
                                        <div class="num">{{ i + 1 }}</div>
                                        {{ popVo.title }}
                                    </a>
                                </dd>
                            </dl>
                        </li>
                    </ul>
                </div> -->

                <div class="popRank">
                    <ul id="best_search">
                        <li>
                            <dl class="time1" style="display:">
                                <dd><a class="t" href="#">
                                        <div class="num">1</div>
                                    </a></dd>
                            </dl>
                            <dl class="time2" style="display:none;">
                                <dd><a class="t" href="#">
                                        <div class="num">2</div>
                                    </a></dd>
                            </dl>
                            <dl class="time3" style="display:none;">
                                <dd><a class="t" href="#">
                                        <div class="num">3</div>
                                    </a></dd>
                            </dl>
                            <dl class="time4" style="display:none;">
                                <dd><a class="t" href="#">
                                        <div class="num">4</div>
                                    </a></dd>
                            </dl>
                            <dl class="time5" style="display:none;">
                                <dd><a class="t" href="#">
                                        <div class="num">5</div>
                                    </a></dd>
                            </dl>
                        </li>
                    </ul>
                </div>

                <!--   -->
                <div class="user-options">
                    <!--   -->
                    <router-link to="/login"
                        v-if="this.$store.state.authUser == null && this.$store.state.auth == null">
                        <img src="@/assets/images/icon_login.svg">
                        <p></p>
                    </router-link>
                    <router-link to="" v-if="this.$store.state.authUser == null && this.$store.state.auth == null"
                        v-on:click="loginForMypage">
                        <img src="@/assets/images/icon_my.svg">
                        <p></p>
                    </router-link>

                    <!-- () -->
                    <router-link to="" v-if="this.$store.state.authUser != null" v-on:click="logout">
                        <img src="@/assets/images/icon_login.svg">
                        <p></p>
                    </router-link>

                    <router-link to="" v-if="this.$store.state.authUser != null" v-on:click="loginForMypage">
                        <img src="@/assets/images/icon_my.svg">
                        <p></p>
                    </router-link>

                    <!-- () -->
                    <router-link to="" v-if="this.$store.state.auth != null" v-on:click="logout">
                        <img src="@/assets/images/icon_login.svg">
                        <p></p>
                    </router-link>

                    <router-link to="" v-if="this.$store.state.auth != null" v-on:click="loginForMypage">
                        <img src="@/assets/images/icon_homepage.png">
                        <p></p>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue';
// import $ from 'jquery';
import $ from 'jquery';

//  
const viewcount = ref(0);
let rtcarousel = null;

// jQuery   
function view(arg) {
    $(".time1, .time2, .time3, .time4, .time5").css("display", "none");
    if (arg === 0) {
        $(".time1").css("display", "block");
    } else if (arg === 1) {
        $(".time2").css("display", "block");
    } else if (arg === 2) {
        $(".time3").css("display", "block");
    } else if (arg === 3) {
        $(".time4").css("display", "block");
    } else if (arg === 4) {
        $(".time5").css("display", "block");
    }
}

// Carousel 
function startCarousel() {
    rtcarousel = setInterval(() => {
        viewcount.value = (viewcount.value + 1) % 5;
        view(viewcount.value);
    }, 2000); //  3000     
}

// Carousel 
function stopCarousel() {
    clearInterval(rtcarousel);
}

//    
onMounted(() => {
    startCarousel();
    $("#best_search").mouseenter(stopCarousel);
    $("#best_search").mouseleave(startCarousel);
});

//    
onBeforeUnmount(() => {
    stopCarousel();
});
</script>

<script>
import "@/assets/css/headerFooter/aheader.css"
import axios from 'axios';

export default {
    name: "AppHeader",
    components: {},
    data() {
        return {
            popList: [],
            popVo: {
                bNo: '',
                title: ''
            }
        };
    },
    methods: {
        // 
        logout() {
            // console.log("");
            this.$store.commit("setAuthUser", null);
            this.$store.commit("setAuth", null);
            this.$store.commit("setToken", null);

            this.$router.push("/");
        },
        // ,  
        loginForMypage() {
            // console.log(" ");
            if (this.$store.state.auth != null) {
                this.$router.push(`/edit/${this.$store.state.auth.bNo}`);
            } else if (this.$store.state.authUser == null) {
                window.alert("    .");
                this.$router.push("/login"); //   
            } else if (this.$store.state.authUser != null) {
                this.$router.push(`/mypage/${this.$store.state.authUser.uNo}`); //     
            }
        },
        getList() {
            console.log(" ");
            axios({
                method: 'get',
                url: `${this.$store.state.apiBaseUrl}/api/poprank`,
                headers: { "Content-Type": "application/json; charset=utf-8" },
                responseType: 'json'
            }).then(response => {
                console.log(response.data.apiData);
                this.popList = response.data.apiData;
            }).catch(error => {
                console.log(error);
            });
        }
    },
    created() {
        this.getList();
    }
};
</script>