<template>
    <div id="wrap">
        <AppHeader />
        <!--    -->
        <SideBar />
        <div class="mypageContainer">
            <div class="container">
                <!--   -->
                <div class="mpReserveConfirmContainer">
                    <!--   -->
                    <div class="mprcTop">
                        <h2></h2>
                        <router-link :to="`/reserve/${uNo}`"> +</router-link>
                    </div>
                    <!--   -->
                    <div class="mprcContents" v-if="reserveVo">
                        <router-link :to="`/reserve/${reserveVo.uNo}`">
                            <div>
                                <p></p>
                                <p>{{ reserveVo.title }}</p>
                            </div>
                            <div>
                                <p></p>
                                <p>{{ reserveVo.dogName }}</p>
                            </div>
                            <div>
                                <p></p>
                                <p>{{ formattedRtDate }} {{ formattedRtTime }}</p>
                            </div>
                            <div>
                                <p></p>
                                <p>{{ totalPrice.toLocaleString() }} </p>
                            </div>
                        </router-link>
                    </div>
                </div>

                <!--  -->
                <div class="mpReviewConfirmContainer" >
                    <!--   -->
                    <div class="mprvcTop">
                        <h2>  </h2>
                        <router-link :to="`/myreview/${uNo}`"> +</router-link>
                    </div>
                    <!--   -->
                    <div class="mprvcContents" v-if="reviewVo">
                        <!--   -->
                        <div class="mprvcContentsInfo">
                            <!--  -->
                            <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${reviewVo.saveName}`">
                            <!-- {{ reviewVo.saveName }} -->
                            <div>
                                <!--  -->
                                <div class="mprvcContentsInfos">
                                    <!--  -->
                                    <p><span> </span> {{ reviewVo.title }}</p>
                                    <p><span> </span> {{ reviewVo.dogName }}</p>
                                    <p><span> </span> {{ totalPrice2.toLocaleString() }} </p>
                                </div>
                                <!--  -->
                                <div class="star-rating">
                                    <div class="star">
                                        <div class="star" style="margin-top: 2px;">
                                            <!-- Full stars -->
                                            <span v-for="i in Math.floor(reviewVo.star)" :key="i"
                                                style="padding: none;"><img src="@/assets/images/star_yellow.jpg"
                                                    style="width: 15px;"></span>
                                            <!-- Half star -->
                                            <!-- Empty stars -->
                                            <span v-for="i in 5 - Math.ceil(reviewVo.star)" :key="'empty_' + i"><img
                                                    src="@/assets/images/star_gray.jpg" style="width: 15px;"></span>
                                            <!-- {{ reviewVo.star }}.0 -->
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <!--    -->
                        <div class="mprvcContentsMine">
                            <p>
                                {{ reviewVo.rContent }}
                            </p>
                        </div>
                    </div>
                </div>

                <!--  -->
                <div class="mpDiaryConfirmContainer">
                    <!--   -->
                    <div class="mpdcTop">
                        <h2></h2>
                        <router-link to="/mydiary"> +</router-link>
                    </div>
                    <!--   -->
                    <div class="mpdcContents" v-if="reserveVo2">
                        <!--   -->
                        <div class="mpdcImg">
                            <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${reserveVo2.saveName}`">
                        </div>
                        <!--   -->
                        <div class="mpdcInfo1">
                            <p><span> :</span> {{ formattedRtDate2 }}</p>
                            <p><span> :</span> {{reserveVo2.dogName}}</p>
                            <p><span> :</span> {{reserveVo2.attitude}} </p>
                            <p><span> :</span> {{reserveVo2.rCondition}}</p>
                            <p><span>() :</span> {{reserveVo2.tangle }}</p>
                            <p><span>  :</span> {{ reserveVo2.disliked }}</p>
                            <p><span>/ :</span> {{ reserveVo2.bath }}</p>
                        </div>
                        <div class="mpdcInfo2">
                            <p><span> :</span> {{reserveVo2.surcharge.toLocaleString()}} </p>
                            <p><span> :</span>
                               {{reserveVo2.message}}
                               
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <TopButton />
        <AppFooter />
    </div>
</template>
<script>
import axios from "axios"
import '@/assets/css/mypage/reserve.css'
import AppHeader from "@/components/AppHeader.vue"
import AppFooter from "@/components/AppFooter.vue"
import SideBar from "@/components/SideBar.vue"
import TopButton from "@/components/TopButton.vue"

export default {
    name: "MypageView",
    components: {
        AppHeader,
        AppFooter,
        SideBar,
        TopButton
    },
    data() {
        return {
            reserveVo: {
                rsNo: 0,
                dogNo: 0,
                rtNo: 0,
                uNo: this.$store.state.authUser.uNo,
                expectedPrice: 0,
                bNo: 0,
                rtDate: "",
                rtTime: "",
                dogName: "",
                title: "",
                surcharge: 0,

            },
            reviewVo: {
                title: "",
                dogNo: 0,
                expectedPrice: 0,
                surcharge: 0,
                bNo: 0,
                rNo: 0,
                uNo: this.$store.state.authUser.uNo,
                rsNo: 0,
                rContent: "",
                rDate: "",
                views: 0,
                riNo: 0,
                star: 0,
                dogName: "",
                saveName: "",
            },
            reserveVo2: {
                uNo: this.$store.state.authUser.uNo,
                rsNo: 0,
                dogNo: 0,
                dogName: "",
                rtNo: 0,
                expectedPrice: 0,
                surcharge: 0,
                attitude: "",
                rCondition: "",
                tangle: "",
                disliked: "",
                bath: "",
                message: "",
                saveName: "",
                bNo:"",
                pushNo:0,

            },
            uNo: this.$store.state.authUser.uNo,

        };
    },
    computed: {
        totalPrice() {
            return this.reserveVo ? this.reserveVo.expectedPrice + this.reserveVo.surcharge : 0;
        },
        totalPrice2() {
            return this.reviewVo ? this.reviewVo.expectedPrice + this.reviewVo.surcharge : 0;
        },
        formattedRtDate() {
            if (!this.reserveVo) return '';
            const date = new Date(this.reserveVo.rtDate);
            return `${date.getFullYear()} ${String(date.getMonth() + 1).padStart(2, '0')} ${String(date.getDate()).padStart(2, '0')}`;
        },
        formattedRtDate2() {
            if (!this.reserveVo2) return '';
            const date = new Date(this.reserveVo2.rtDate);
            return `${date.getFullYear()} ${String(date.getMonth() + 1).padStart(2, '0')} ${String(date.getDate()).padStart(2, '0')}`;
        },
        formattedRtTime() {
            if (!this.reserveVo || !this.reserveVo.rtTime) return '';
            const time = this.reserveVo.rtTime;
            const [hours, minutes] = time.split(':');
            return `${hours.padStart(2, '0')}:${minutes.padStart(2, '0')}`;
        },
    },
    methods: {
        getReverve() {
            console.log(" ");

            axios({
                method: 'get',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/api/mypage/reserve`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { uNo: this.reserveVo.uNo },
                responseType: 'json' //
            }).then(response => {
                console.log("  ");
                console.log(response.data.apiData); //
                this.reserveVo = response.data.apiData;

            }).catch(error => {
                console.log(error);
            });
        },
        getReview() {
            console.log(" ");
            axios({
                method: 'get',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/api/mypage/review`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { uNo: this.reviewVo.uNo },
                responseType: 'json' //
            }).then(response => {
                console.log("  ");
                console.log(response.data.apiData); //
                this.reviewVo = response.data.apiData;

            }).catch(error => {
                console.log(error);
            });
        },
        getDiary() {
            console.log("");
            axios({
                method: 'get',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/api/mypage/mydiary`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { uNo: this.reviewVo.uNo },
                responseType: 'json' //
            }).then(response => {
                console.log("  ");
                console.log(response.data.apiData); //
                this.reserveVo2 = response.data.apiData;

            }).catch(error => {
                console.log(error);
            });
        }
    },
    created() {
        this.getReverve();
        this.getReview();
        this.getDiary();
    }
};
</script>
<style></style>